/* rubik-300normal - latin */
@font-face {
  font-family: 'Rubik';
  font-style: normal;
  font-display: swap;
  font-weight: 300;
  src:
    local('Rubik Light '),
    local('Rubik-Light'),
    url(https://github.e36.io/static/media/rubik-latin-300.239cf859.woff2) format('woff2'), 
    url(https://github.e36.io/static/media/rubik-latin-300.73ce1e5f.woff) format('woff'); /* Modern Browsers */
}

